html, body, html *, *, *::before, *::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}

body.no-scroll {
  overflow: hidden;
}

.wrapper {
  overflow: hidden;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  width: 100%;
}
.wrapper main {
  flex: 1;
}

img {
  max-width: 100%;
}

a {
  text-decoration: none;
  color: inherit;
}

button {
  cursor: pointer;
  background: transparent;
  border: none;
  color: inherit;
}
button:disabled {
  cursor: unset;
}

span {
  color: inherit;
  font-size: inherit;
}

li {
  list-style: none;
}

input, textarea {
  outline: none;
  border: none;
}

input[type=number]::-webkit-outer-spin-button,
input[type=number]::-webkit-inner-spin-button {
  appearance: none;
}

[class*=icon-]::before {
  width: auto;
  margin-left: 0;
  margin-right: 0;
}

* {
  font-family: Lexend, sans-serif;
  font-weight: 300;
  font-size: clamp(14px, 0.962rem + 0.16vw, 18px);
  line-height: 150%;
  color: #E4E4E7;
}

html, body {
  background-color: #1A1A1A;
  color: #B3B3B3;
}

[class*=__container], .container {
  padding: 0 clamp(1rem, -0.02rem + 4.18vw, 5rem);
  margin: 0 auto;
  max-width: 1676px;
  min-width: 360px;
}

.logo {
  display: flex;
}
.logo img {
  height: clamp(26px, 1.36rem + 1.09vw, 40px);
}

.section {
  margin-bottom: clamp(80px, 3.673rem + 5.44vw, 150px);
}

.note {
  font-size: clamp(12px, 0.636rem + 0.47vw, 18px);
  line-height: 1.33;
  color: #FFFFFF;
  display: inline-flex;
  gap: clamp(4px, 0.212rem + 0.16vw, 6px);
  align-items: center;
  border-radius: 100ch;
  padding: 0.56em 1.11em 0.56em 0.67em;
  background: #262626;
}
@media (max-width: 840px) {
  .note {
    padding: 8px 16px 8px 10px;
  }
}

.title {
  font-weight: 500;
  font-size: clamp(28px, 1.371rem + 1.56vw, 48px);
  color: #FFFFFF;
  margin-bottom: 0.29em;
}
.title strong {
  font-size: inherit;
  font-weight: inherit;
  color: #CAFF33;
}

.contacts {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px;
}
.contacts a {
  display: flex;
  align-items: center;
  gap: 0.33em;
}
.contacts a span {
  color: #E4E4E7;
  transition: 0.2s ease;
}
@media (any-hover: hover) {
  .contacts a:hover span {
    color: #FFFFFF;
  }
}
.contacts span {
  font-size: inherit;
}

.socials {
  display: flex;
  gap: clamp(8px, -1.788rem + 2.54vw, 14px);
}
.socials a {
  display: inline-flex;
  padding: 12px;
  border-radius: 100%;
  background: #CAFF33;
  transition: 0.2s ease;
}
@media (any-hover: hover) {
  .socials a:hover {
    background: #D8FF66;
  }
}

.legall-links {
  display: flex;
  gap: 0.66em;
}
.legall-links a {
  border-right: 1px solid #B3B3B3;
  padding-right: 0.66em;
  transition: 0.2s ease;
}
@media (any-hover: hover) {
  .legall-links a:hover {
    color: #FFFFFF;
  }
}
.legall-links a:last-child {
  border-right: none;
  padding-right: 0;
}

.round-icon {
  position: relative;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  background: linear-gradient(180deg, rgba(202, 255, 51, 0.05) 0%, rgba(202, 255, 51, 0) 100%);
  border-radius: 100%;
  width: clamp(72px, 2.212rem + 2.54vw, 78px);
  height: clamp(72px, 2.212rem + 2.54vw, 78px);
}
.round-icon svg {
  height: 44%;
  width: 44%;
}
.round-icon::before {
  position: absolute;
  content: "";
  width: 72%;
  height: 72%;
  border-radius: 100%;
  background: linear-gradient(180deg, rgba(202, 255, 51, 0.1) -66.22%, rgba(202, 255, 51, 0) 85.46%);
}

.tag {
  color: #B3B3B3;
  display: inline-block;
  padding: 0.44em 0.89em;
  background: #1A1A1A;
  border: 1px solid #262626;
  border-radius: 100ch;
}

.nav__list {
  display: flex;
  align-items: center;
  gap: 18px 26px;
}
.nav__list-item {
  display: inline-block;
  padding: 0.67em 0;
  border-radius: 100ch;
  background: transparent;
  transition: 0.2s ease;
}
.nav__list-item a {
  font-weight: 400;
  font-size: clamp(14px, -0.65rem + 1.69vw, 18px);
  color: #FFFFFF;
}
.nav__list-item._active {
  pointer-events: none;
  padding: 0.67em 1.33em;
  background: #262626;
}
@media (any-hover: hover) {
  .nav__list-item:hover {
    padding: 0.67em 1.33em;
    background: #262626;
  }
}

.btn {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  font-weight: 400;
  text-align: center;
  font-size: clamp(14px, 0.799rem + 0.31vw, 18px);
  color: #1C1C1C;
  padding: 1em 1.67em;
  background: #CAFF33;
  border-radius: 100ch;
  transition: 0.2s ease;
  gap: 4px;
}
@media (any-hover: hover) {
  .btn:hover {
    background: #D8FF66;
  }
}
.btn_black {
  padding: clamp(14px, 0.799rem + 0.31vw, 18px) clamp(20px, 1.174rem + 0.31vw, 24px);
  background: #1C1C1C;
  border: 1px solid #262626;
  border-radius: 100ch;
  color: white;
}
@media (any-hover: hover) {
  .btn_black:hover {
    background: #1C1C1C;
    border: 1px solid #262626;
    color: #CAFF33;
  }
}
.btn_round {
  width: 62px;
  height: 62px;
  padding: 0;
  border-radius: 50%;
  background: #1C1C1C;
  border: 1px solid #262626;
}
.btn_round svg {
  width: 55%;
  height: 55%;
  fill: #CAFF33;
}
.btn_round svg path {
  fill: #CAFF33;
  transition: 0.2s ease;
}
@media (any-hover: hover) {
  .btn_round:hover {
    background: #1C1C1C;
    border: 1px solid #262626;
  }
  .btn_round:hover svg path {
    fill: white;
  }
}

.input {
  position: relative;
  display: inline-flex;
  flex-direction: column;
}
.input input {
  padding: clamp(16px, 0.848rem + 0.62vw, 24px);
  background: #1A1A1A;
  border: 1px solid #262626;
  border-radius: 100ch;
  color: #e4e4e7;
  min-width: 298px;
}
.input input::placeholder {
  color: #59595A;
}
.input_password input {
  padding-right: clamp(46px, 2.648rem + 0.93vw, 58px);
}
.input_password button {
  position: absolute;
  display: flex;
  top: 50%;
  transform: translateY(-50%);
  right: clamp(16px, 0.848rem + 0.62vw, 24px);
}
.input_password button svg {
  width: clamp(20px, 1.174rem + 0.31vw, 24px);
}

.cta {
  display: flex;
  align-items: center;
  gap: clamp(20px, -2.564rem + 4.24vw, 30px);
}
.cta__signup-btn {
  font-weight: 400;
  font-size: clamp(14px, -0.65rem + 1.69vw, 18px);
  color: #FFFFFF;
  padding: 0;
  background: transparent;
  transition: 0.2s ease;
}
@media (any-hover: hover) {
  .cta__signup-btn:hover {
    color: #D8FF66;
  }
}
.cta__login-btn {
  font-weight: 400;
  font-size: clamp(14px, -0.65rem + 1.69vw, 18px);
  padding: 0.78em 1.67em;
}

.burger-btn {
  position: relative;
  display: none;
  width: 56px;
  height: 40px;
}
.burger-btn span {
  position: absolute;
  width: 21px;
  height: 3px;
  border-radius: 2px;
  background-color: #333333;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  transition: 0.2s ease;
}
.burger-btn span:nth-child(1) {
  transform: translate(-50%, -50%) translateY(-6px);
}
.burger-btn span:nth-child(3) {
  transform: translate(-50%, -50%) translateY(6px) translateX(5px);
  width: 11px;
}
.burger-btn._active span:nth-child(1) {
  transform: translate(-50%, -50%) translateY(-8px) translateX(-3px) rotate(-45deg);
  transform-origin: right;
  width: 22px;
}
.burger-btn._active span:nth-child(2) {
  transform: translate(-50%, -50%) translateY(8px) translateX(-3px) rotate(45deg);
  transform-origin: right;
  width: 22px;
}
.burger-btn._active span:nth-child(3) {
  transform: translate(-50%, -50%);
  width: 0;
}

.menu {
  margin-bottom: 0;
  display: flex;
  flex-direction: column;
  padding: 0 clamp(14px, 0.496rem + 1.56vw, 34px);
  background: #1C1C1C;
  border: 0px solid #262626;
  border-radius: 20px;
  overflow: hidden;
  max-height: 0;
  opacity: 0;
  transition: 0.3s ease;
}
.menu .nav {
  margin-bottom: 20px;
}
.menu .nav__list {
  flex-direction: column;
  align-items: stretch;
}
.menu .nav__list-item {
  padding: 0;
  background: transparent;
  transition: 0.2s ease;
}
.menu .nav__list-item a {
  font-weight: 400;
  font-size: 18px;
  color: #FFFFFF;
}
.menu._active {
  opacity: 1;
  border-width: 1px;
  padding: 20px clamp(14px, 0.496rem + 1.56vw, 34px);
  max-height: 1000px;
  margin-bottom: clamp(30px, -5.752rem + 8.47vw, 50px);
}

.mini-app {
  position: relative;
  width: clamp(305px, 15.082rem + 16.33vw, 515px);
}
.mini-app__wrapper {
  position: relative;
  z-index: 2;
  display: flex;
  flex-direction: column;
  font-size: clamp(14px, 0.818rem + 0.23vw, 17px);
  padding: 2.06em;
  border-radius: 0.59em;
  border: 1px solid transparent;
  background: linear-gradient(214.24deg, rgba(202, 255, 51, 0) 91.73%, rgba(202, 255, 51, 0.2) 99.95%) border-box, linear-gradient(214.24deg, rgba(202, 255, 51, 0.2) 0%, rgba(202, 255, 51, 0) 11.24%) border-box, #262626 border-box;
}
.mini-app__wrapper::before {
  position: absolute;
  z-index: 3;
  content: "";
  inset: 0;
  margin: 1px;
  border-radius: inherit;
  background: linear-gradient(214.85deg, rgba(26, 26, 26, 0) 0%, #1A1A1A 39.72%), url(../img/bg/texture.webp), #1A1A1A;
  background-size: 17.12em auto;
}
.mini-app__wrapper * {
  position: relative;
  z-index: 3;
}
.mini-app__image {
  position: absolute;
  z-index: 1;
  width: clamp(220px, 9.997rem + 15.4vw, 418px);
  right: -187px;
  top: 10px;
}
@media (max-width: 1440px) {
  .mini-app__image {
    right: -131px;
    top: -6px;
  }
}
@media (max-width: 460px) {
  .mini-app__image {
    right: -21px;
    top: -61px;
  }
}
.mini-app .income {
  position: absolute;
  z-index: 4;
  top: -2.53em;
  left: -3.59em;
}
@media (max-width: 410px) {
  .mini-app .income {
    left: -10px;
  }
}
.mini-app__title {
  font-weight: 500;
  font-size: inherit;
  color: #FFFFFF;
  margin-bottom: 1em;
}
.mini-app__transactions {
  margin-bottom: 1.53em;
}
.mini-app__transactions .transaction {
  transform-origin: top center;
}
.mini-app__transactions .transaction:nth-child(2) {
  margin-top: -1.5em;
  z-index: -1;
  transform: scale(0.92);
  opacity: 50%;
}
.mini-app__transactions .transaction:nth-child(3) {
  margin-top: -1.5em;
  z-index: -2;
  opacity: 20%;
  transform: scale(0.84);
}
.mini-app .support-currency {
  position: absolute;
  z-index: 4;
  right: -3.25em;
  bottom: -0.81em;
  transform: translateY(100%);
}
@media (max-width: 410px) {
  .mini-app .support-currency {
    right: -10px;
  }
}

.income {
