  gap: 30px clamp(40px, -1.314rem + 4.24vw, 50px);
}
@media (max-width: 980px) {
  .products {
    flex-direction: column;
  }
}
.products__item {
  text-align: center;
  border-right: 1px solid #262626;
  padding-right: clamp(40px, -1.314rem + 4.24vw, 50px);
}
.products__item:last-child {
  border-right: none;
  padding-right: 0;
}
@media (max-width: 980px) {
  .products__item {
    border-right: none;
    border-bottom: 1px solid #262626;
    padding-right: 0;
    padding-bottom: 30px;
  }
  .products__item:last-child {
    border-bottom: none;
    padding-bottom: 0;
  }
}
.products__title {
  font-weight: 400;
  font-size: clamp(20px, -0.275rem + 1.69vw, 24px);
  color: #FFFFFF;
  margin-top: 1em;
  margin-bottom: 0.67em;
}
.cases .title {
  margin-bottom: 0.29em;
}
.cases__content {
  margin-top: clamp(60px, 3.371rem + 1.56vw, 80px);
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: clamp(40px, -5.127rem + 8.47vw, 60px) clamp(60px, -11.504rem + 16.95vw, 100px);
}
@media (max-width: 1370px) {
  .cases__content {
    display: flex;
    flex-direction: column;
  }
}
.cases__content .cases-list:nth-child(3) {
  grid-column: 2;
  grid-row: 2;
}
.cases__content .cases-list:nth-child(3)::before {
  transform: scaleX(-1);
}
@media (max-width: 1370px) {
  .cases__content .cases-list:nth-child(3)::before {
    transform: none;
  }
}
.cases__content .cases-content:nth-child(4) {
  grid-column: 1;
  grid-row: 2;
}

.features__title {
  margin-bottom: 0.29em;
}
@media (max-width: 1260px) {
  .features__title {
    text-align: center;
  }
}
@media (max-width: 1260px) {
  .features__descr {
    text-align: center;
  }
}
.faq__container {
  display: flex;
  flex-direction: column;
}
@media (max-width: 1130px) {
  .faq__container {
    text-align: center;
  }
}
.faq__list {
  text-align: left;
  position: relative;
  margin-top: clamp(50px, 2.556rem + 2.33vw, 80px);
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: clamp(20px, -2.564rem + 4.24vw, 30px);
  overflow: hidden;
  max-height: clamp(659px, 2.671rem + 42.8vw, 760px);
  transition: 0.4s ease;
}
@media (max-width: 1130px) {
  .faq__list {
    display: flex;
    flex-direction: column;
    max-height: 1379px;
  }
}
.faq__list::before {
  position: absolute;
  content: "";
  bottom: 0;
  left: 0;
  right: 0;
  height: 36%;
  max-height: 275px;
  transition: 0.2s ease;
  opacity: 1;
  background: linear-gradient(180deg, rgba(25, 25, 25, 0) 0%, #1A1A1A 100%);
}
.faq__list._opened {
  max-height: 10000px;
}
.faq__list._opened::before {
  opacity: 0;
  pointer-events: none;
}
.faq__btn {
  align-self: center;
}
.faq__btn:disabled {
  display: none;
}

.testim__slider {
  position: relative;
}
.testim .swiper {
  position: relative;
  overflow-x: hidden;
  touch-action: pan-y;
  display: flex;
  margin: 0 112px;
}
.testim .swiper::before, .testim .swiper::after {
  pointer-events: none;
  position: absolute;
  z-index: 3;
  content: "";
  top: 0;
  bottom: 0;
  width: 33%;
  background: linear-gradient(270deg, #1A1A1A 10.8%, rgba(25, 25, 25, 0) 105.84%);
}
.testim .swiper::before {
  left: 0;
  transform: scaleX(-1);
}
.testim .swiper::after {
  right: 0;
}
@media (max-width: 850px) {
  .testim .swiper {
    margin: 0;
  }
}
.testim .swiper-wrapper {
  display: flex;
}
.testim__slider-btns {
  display: flex;
  justify-content: center;
  gap: 20px;
}
@media (max-width: 850px) {
  .testim__slider-btns {
    margin-top: 30px;
  }
}
.testim__slider-btn {
  position: absolute;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
}
.testim__slider-btn:disabled {
  opacity: 0;
  pointer-events: none;
}
.testim__slider-btn:nth-child(2) {
  left: auto;
  right: 0;
}
.testim__slider-btn:nth-child(2) svg {
  transform: scaleX(-1);
}
@media (max-width: 850px) {
  .testim__slider-btn {
    position: static;
    transform: none;
  }
}

.cta-sect__wrapper {
  position: relative;
  display: flex;
  gap: 24px 150px;
  align-items: center;
  padding: clamp(30px, 0.927rem + 3.89vw, 80px);
  border-radius: 20px;
  background: linear-gradient(95.43deg, rgba(202, 255, 51, 0.2) 0%, rgba(202, 255, 51, 0) 5.36%), #262626;
}
.cta-sect__wrapper::before {
  position: absolute;
  content: "";
  inset: 1px;
  border-radius: inherit;
  background: url(../img/bg/bg2.svg) no-repeat, linear-gradient(267.5deg, #1C1C1C 40.67%, rgba(28, 28, 28, 0) 99.81%), url(../img/bg/texture.webp), #1C1C1C;
  background-size: clamp(151px, 6.651rem + 11.43vw, 298px), auto, 298px;
}
.cta-sect__wrapper > * {
  position: relative;
}
@media (max-width: 900px) {
  .cta-sect__wrapper {
    flex-direction: column;
    text-align: center;
  }
}
.cta-sect__title {
  font-size: clamp(24px, 1.197rem + 1.24vw, 40px);
  margin-bottom: 0.35em;
}
.cta-sect__btn {
  flex: 0 0 content;
}

@media (max-width: 1000px) {
