  }
  .vision__content .vision-item:nth-child(2) {
    flex-direction: row-reverse;
  }
  .vision__content .vision-item:nth-child(2) .vision-item__content {
    border-right: 1px solid #CAFF33;
    border-left: none;
    padding-right: clamp(40px, -1.314rem + 4.24vw, 50px);
    padding-left: 0;
    margin-top: clamp(84px, -0.852rem + 6.78vw, 100px);
    transform: translateY(calc(-0.5 * clamp(84px, -0.852rem + 6.78vw, 100px)));
  }
}

@media (max-width: 980px) {
  .releases__container {
    text-align: center;
  }
}
.releases__content {
  text-align: left;
  display: grid;
