.account__wrapper {
  position: relative;
  padding: 100px 150px;
  max-width: clamp(1064px, -33.415rem + 111.02vw, 1326px);
  margin: 0 auto;
  text-align: center;
  background: url(../img/bg/texture2.webp) no-repeat center, #1C1C1C;
  background-size: cover;
  background-blend-mode: overlay, normal;
  border: 1px solid #262626;
  border-radius: 20px;
}
.account__wrapper::before {
  position: absolute;
  content: "";
  inset: 0;
  background: url(../../img/bg/bg2.svg) no-repeat;
  background-size: clamp(167px, 7.121rem + 13.61vw, 342px);
  transform: scaleX(-1);
}
.account__wrapper > * {
  position: relative;
}
@media (max-width: 1440px) {
  .account__wrapper {
    padding: 80px 150px;
  }
}
@media (max-width: 1050px) {
  .account__wrapper {
    padding: 30px;
  }
}
.account__form {
  margin-top: clamp(40px, 1.742rem + 3.11vw, 80px);
}
.account__inputs {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 30px clamp(24px, -0.788rem + 2.54vw, 30px);
}
@media (max-width: 1050px) {
  .account__inputs {
    display: flex;
    flex-direction: column;
    row-gap: 20px;
  }
}
.account__form-bottom {
