  .account__form-bottom {
    max-width: none;
  }
}
.account__form-link {
  margin-bottom: clamp(24px, 1.197rem + 1.24vw, 40px);
  margin-top: clamp(24px, 1.197rem + 1.24vw, 40px);
  transition: 0.2s ease;
}
@media (any-hover: hover) {
  .account__form-link:hover {
    color: white;
  }
}
.account__form-btn {
  margin-bottom: 24px;
}
.account__form-btn.btn_black {
  background: #262626;
  border: 1px solid #333333;
}
.account__form-line-text {
  display: flex;
  align-items: center;
  gap: clamp(16px, -0.525rem + 1.69vw, 20px);
}
.account__form-line-text::before, .account__form-line-text::after {
  display: block;
  content: "";
  flex: 1 0 auto;
  height: 0;
  border-top: 1px solid #B3B3B3;
}
.account__form-link-icons {
  margin-top: clamp(24px, 1.386rem + 0.47vw, 30px);
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: clamp(20px, -0.275rem + 1.69vw, 24px);
}
.account__form-link-icon {
  transition: 0.2s ease;
}
@media (any-hover: hover) {
  .account__form-link-icon:hover {
    transform: scale(1.2);
  }
}